:root {
  --fs-1: 3.13em;
  --fs-2: 2.5em;
  --fs-3: 1.56em;
  --fs-4: 1.56em;
  --fs-5: .75em;
  --fs-6: 1em;
  --fs-7: .63em;
}

@media screen and (min-width: 35.5em) {
  :root {
    --fs-1: 6.25em;
    --fs-2: 3.13em;
    --fs-3: 1.94em;
    --fs-4: 1.56em;
    --fs-5: 1.25em;
    --fs-6: .81em;
    --fs-7: .63em;
  }
}

.image-gallery {
  grid-gap: 1em;
  box-sizing: border-box;
  grid-template-rows: 1fr;
  display: grid;
}

.image-gallery img {
  object-fit: cover;
  object-position: center;
  -webkit-user-select: none;
  user-select: none;
  width: 100%;
  height: 100%;
}

.image-gallery__main {
  box-sizing: border-box;
  display: flex;
  position: relative;
  overflow: hidden;
}

.image-gallery__main .swiper-wrapper {
  aspect-ratio: 1;
}

.image-gallery__main .swiper-slide img {
  object-position: center;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.image-gallery__main .swiper-button-prev, .image-gallery__main .swiper-button-next {
  -webkit-user-select: none;
  user-select: none;
  display: none;
  bottom: 1.5em;
}

.image-gallery__main .swiper-button-prev {
  left: 1.5em;
}

.image-gallery__main .swiper-button-next {
  right: 1.5em;
}

.image-gallery__thumbs {
  box-sizing: border-box;
  overflow-x: scroll;
  overflow-y: hidden;
}

.image-gallery__thumbs::-webkit-scrollbar {
  display: none;
}

.image-gallery__thumbs .swiper-wrapper {
  justify-content: flex-start;
  align-items: center;
  gap: .5em;
  display: flex;
}

.image-gallery__thumbs .swiper-slide {
  border: 1px solid #0000;
  width: 64px;
  height: 64px;
  padding: 6px;
  transition: border .12s ease-in-out;
}

.image-gallery__thumbs .swiper-slide.swiper-slide-thumb-active {
  border-color: #201d1f;
}

.image-gallery__thumbs .swiper-slide img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.image-gallery__main__controll--left, .image-gallery__main__controll--right {
  -webkit-user-select: none;
  user-select: none;
  z-index: 2;
  color: #596679;
  transition: all .12s ease-in-out;
  position: absolute;
  bottom: 1em;
}

.image-gallery__main__controll--left:hover, .image-gallery__main__controll--right:hover {
  color: #59667980;
}

.main-info {
  flex-direction: column;
  display: flex;
}

.price {
  font-size: var(--fs-4) !important;
}

.color-picker {
  gap: .5em;
  margin-block: 1em;
  display: flex;
}

.color-picker__ratio {
  border: 1px solid #0000;
  width: 40px;
  height: 40px;
  padding: 6px;
  transition: border .12s ease-in-out;
  position: relative;
  overflow: hidden;
}

.color-picker__ratio img {
  -webkit-user-select: none;
  user-select: none;
  object-fit: cover;
  object-position: center;
  z-index: -1;
  width: calc(100% - 12px);
  height: calc(100% - 12px);
  position: absolute;
  top: 6px;
  left: 6px;
}

.color-picker__ratio input {
  opacity: 0;
  background: none;
  width: 100%;
  height: 100%;
}

.color-picker__ratio:has(input:checked) {
  border-color: #000;
}

.description-info__info ul {
  width: min(12em, 30%);
  padding-left: 0;
}

.description-info__info li {
  margin-block: 1em;
  display: flex;
}

.description-info__info li strong, .description-info__info li p {
  flex-grow: 1;
  flex-basis: 0;
  margin: 0;
}

.description-info__info li strong {
  flex-shrink: 0;
}

.description-info__info li p {
  flex-shrink: 1;
}

.swiper-per-view {
  margin-inline: auto;
  position: relative;
  overflow: hidden;
}

.swiper-per-view .swiper-slide {
  --swiper-slide-number: 1;
  --swiper-slide-gap: 60px;
  width: calc(100% / var(--swiper-slide-number)  - var(--swiper-slide-gap) / var(--swiper-slide-number) * (var(--swiper-slide-number)  - 1));
  box-sizing: border-box;
}

@media screen and (min-width: 35.5em) {
  .image-gallery__main .swiper-button-prev, .image-gallery__main .swiper-button-next {
    display: flex;
  }

  .image-gallery__thumbs .swiper-wrapper {
    gap: 1em;
  }

  .main-info e p + p {
    padding-top: 1em;
    position: relative;
  }

  .main-info e p + p:after {
    content: "";
    background-color: #201d1f;
    width: 5em;
    height: 2px;
    position: absolute;
    top: 0;
    left: 0;
  }

  .price {
    font-size: var(--fs-2) !important;
  }

  .color-picker {
    gap: 1em;
  }

  .color-picker__ratio {
    width: 5em;
    height: 5em;
  }

  .stepper-input {
    display: flex;
  }

  .swiper-per-view {
    margin-bottom: 1em;
  }

  .swiper-per-view .swiper-slide {
    --swiper-slide-number: 2;
  }
}

@media screen and (min-width: 64em) {
  .swiper-per-view .swiper-slide {
    --swiper-slide-number: 3;
  }
}

