.image-gallery__main {
  .swiper-button-prev,
  .swiper-button-next {
    display: flex;
  }
}

.image-gallery__thumbs {
  .swiper-wrapper {
    gap: 1em;
  }
}

.main-info {
  e p + p {
    position: relative;
    padding-top: 1em;
    &::after {
      content: "";
      height: 2px;
      width: 5em;
      position: absolute;
      top: 0;
      left: 0;
      background-color: $black;
    }
  }
}

.price {
  font-size: var(--fs-2) !important;
}

.color-picker {
  gap: 1em;
}

.color-picker__ratio {
  width: 5em;
  height: 5em;
}

.stepper-input {
  display: flex;
}

.swiper-per-view {
  // max-width: 90%;
  margin-bottom: 1em;
  .swiper-slide {
    --swiper-slide-number: 2;
    // --swiper-slide-gap: 60px;
  }
}
