@import "media";

// font sizes
$font-size-base: 16px;

$font-sizes-mobile: (
  1: 50px,
  2: 40px,
  3: 25px,
  4: 25px,
  5: 12px,
  6: 16px,
  7: 10px,
);

$font-sizes-desktop: (
  1: 100px,
  2: 50px,
  3: 31px,
  4: 25px,
  5: 20px,
  6: 13px,
  7: 10px,
);

@each $name, $size in $font-sizes-mobile {
  :root {
    @each $name, $size in $font-sizes-mobile {
      --fs-#{$name}: #{calc(
          round(calc($size / $font-size-base * 1em * 100)) / 100
        )};
    }
  }
}

@include sm {
  :root {
    @each $name, $size in $font-sizes-desktop {
      --fs-#{$name}: #{calc(
          round(calc($size / $font-size-base * 1em * 100)) / 100
        )};
    }
  }
}

// font weights
$font-weights: (
  4: 400,
  7: 700,
  9: 900,
);

// variables
$fw-4: map-get($font-weights, 4);
$fw-7: map-get($font-weights, 7);
$fw-9: map-get($font-weights, 9);

// colors
$colors: (
  "white": #ffffff,
  "black": #201d1f,
  "dark": #000,
  "primary-50": #ebf0ff,
  "primary-100": #ffe2f2,
  "primary-300": #ffb5dd,
  "primary-500": #ff56b1,
);

// variables
$white: map-get($colors, "white");
$black: map-get($colors, "black");
$dark: map-get($colors, "dark");
$primary-50: map-get($colors, "primary-50");
$primary-100: map-get($colors, "primary-100");
$primary-300: map-get($colors, "primary-300");
$primary-500: map-get($colors, "primary-500");
