@import "variables";

.image-gallery {
  display: grid;
  grid-template-rows: 1fr;
  grid-gap: 1em;
  box-sizing: border-box;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    user-select: none;
  }
}
.image-gallery__main {
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  display: flex;

  .swiper-wrapper {
    aspect-ratio: 1;
  }

  .swiper-slide {
    img {
      width: 100%;
      height: 100%;
      object-position: center;
      object-fit: cover;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    display: none;
    bottom: 1.5em;
    user-select: none;
  }
  .swiper-button-prev {
    left: 1.5em;
  }
  .swiper-button-next {
    right: 1.5em;
  }
}
.image-gallery__thumbs {
  box-sizing: border-box;
  overflow-y: hidden;
  overflow-x: scroll;
  &::-webkit-scrollbar {
    display: none;
  }

  .swiper-wrapper {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 0.5em;
  }

  .swiper-slide {
    width: 64px;
    height: 64px;
    padding: 6px;
    border: 1px solid transparent;
    transition: border ease-in-out 0.12s;

    &.swiper-slide-thumb-active {
      border-color: $black;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
.image-gallery__main__controll--left,
.image-gallery__main__controll--right {
  position: absolute;
  bottom: 1em;
  transition: all ease-in-out 0.12s;
  user-select: none;
  z-index: 2;
  & {
    color: #596679;
  }
  &:hover {
    color: rgba($color: #596679, $alpha: 0.5);
  }
}

.main-info {
  display: flex;
  flex-direction: column;
}

.price {
  font-size: var(--fs-4) !important;
}

.color-picker {
  display: flex;
  gap: 0.5em;
  margin-block: 1em;
}

.color-picker__ratio {
  width: 40px;
  height: 40px;
  position: relative;
  overflow: hidden;
  padding: 6px;
  border: 1px solid transparent;
  transition: border ease-in-out 0.12s;

  img {
    width: calc(100% - 12px);
    height: calc(100% - 12px);
    position: absolute;
    left: 6px;
    top: 6px;
    user-select: none;
    // opacity: 0.5;
    object-fit: cover;
    object-position: center;
    z-index: -1;
  }
  input {
    width: 100%;
    height: 100%;
    opacity: 0;
    background: none;
  }

  &:has(input:checked) {
    border-color: black;
  }
}

.description-info__info {
  ul {
    width: min(12em, 30%);
    padding-left: 0;
  }
  li {
    display: flex;
    margin-block: 1em;
    strong,
    p {
      margin: 0;
      flex-basis: 0px;
      flex-grow: 1;
    }
    strong {
      flex-shrink: 0;
    }
    p {
      flex-shrink: 1;
    }
  }
}

.swiper-per-view {
  position: relative;
  overflow: hidden;
  margin-inline: auto;
  .swiper-slide {
    --swiper-slide-number: 1;
    --swiper-slide-gap: 60px;
    width: calc(
      100% / var(--swiper-slide-number) - var(--swiper-slide-gap) /
        var(--swiper-slide-number) * (var(--swiper-slide-number) - 1)
    );
    box-sizing: border-box;
  }
}

@include sm {
  @import "item-page-sm";
}

@include lg {
  .swiper-per-view {
    .swiper-slide {
      --swiper-slide-number: 3;
    }
  }
}
