// from / until
@mixin from($breakpoints) {
  @media screen and (min-width: $breakpoints) {
    @content;
  }
}

@mixin until($breakpoints) {
  @media screen and (max-width: $breakpoints) {
    @content;
  }
}

@mixin from-until($breakpoint-from, $breakpoint-until) {
  @media screen and (min-width: $breakpoint-from) and (max-width: $breakpoint-until) {
    @content;
  }
}

// predefined breakpoints
$breakpoints: (
  "sm": 35.5em,
  // 568px
  "md": 48em,
  // 768px
  "lg": 64em,
  // 1024px
  // "xl": 80em,      // 1280px
  // "xxl": 120em,    // 1920px
  // "xxxl": 160em,   // 2560px
  // "x4k": 240em,    // 3840px  ,,
);

@mixin sm {
  @media screen and (min-width: map-get($breakpoints, "sm")) {
    @content;
  }
}

@mixin md {
  @media screen and (min-width: map-get($breakpoints, "md")) {
    @content;
  }
}

@mixin lg {
  @media screen and (min-width: map-get($breakpoints, "lg")) {
    @content;
  }
}

@mixin xl {
  @media screen and (min-width: map-get($breakpoints, "xl")) {
    @content;
  }
}

@mixin xxl {
  @media screen and (min-width: map-get($breakpoints, "xxl")) {
    @content;
  }
}

@mixin xxxl {
  @media screen and (min-width: map-get($breakpoints, "xxxl")) {
    @content;
  }
}

@mixin x4k {
  @media screen and (min-width: map-get($breakpoints, "x4k")) {
    @content;
  }
}
